import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-tr';

const Tr = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <tr
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </tr>
));

Tr.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

Tr.displayName = 'Tr';

export default Tr;
