import React, { forwardRef } from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-screen-reader';

const ScreenReader = forwardRef(({ children, className, tag: Tag, ...remainingProps }, ref) => (
  <Tag
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </Tag>
));

ScreenReader.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType
};

ScreenReader.defaultProps = {
  tag: 'span'
};

ScreenReader.displayName = 'ScreenReader';

export default ScreenReader;
