import React, { forwardRef } from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-td';

const Td = forwardRef(({
  children,
  className,
  textAlign,
  ...remainingProps
}, ref) => (
  <td
    className={classnames(CSS_PREFIX, {
      [`vds-text--${textAlign}`]: Boolean(textAlign)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </td>
));

Td.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Text alignment at all sizes
   */
  textAlign: oneOf(['center', 'left', 'right'])
};

Td.displayName = 'Td';

export default Td;
