import React, { Children, cloneElement, forwardRef, useEffect, useRef, useState } from 'react';
import { node, number, oneOf, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import TabItem from '../tab-item';

const CSS_PREFIX = 'vds-tab';
const KEY_CODES = {
  ARROW_DOWN: 40,
  ARROW_LEFT: 37,
  ARROW_RIGHT: 39,
  ARROW_UP: 38,
  END: 35,
  HOME: 36
};

const TabList = forwardRef(({
  children,
  className,
  orientation,
  role,
  selectedTab,
  ...remainingProps
}, ref) => {
  ref = ref || useRef();
  const [selectedTabIndex, setSelectedTabIndex] = useState(selectedTab);
  const [focusedIndex, setFocusedIndex] = useState(selectedTabIndex);
  const [focusableChildren, setFocusChildren] = useState([]);

  useEffect(() => {
    setFocusChildren(Array.from(
      ref.current.querySelectorAll('li > .vds-tab:not(.vds-state--disabled)')
    ));
  }, [children, ref]);

  useEffect(() => {
    setSelectedTabIndex(selectedTab);
  }, [selectedTab]);

  const handleClick = (e, k, child) => {
    setSelectedTabIndex(k);
    setFocusedIndex(k);

    if (typeof child.props.onClick === 'function') {
      child.props.onClick(e, k);
    }
  };

  const handleKeyDown = (e, k, child) => {
    const code = e.charCode || e.keyCode;
    const count = focusableChildren.length;
    const fIndex = focusedIndex || 0;

    switch (code) {
      case KEY_CODES.END: {
        e.preventDefault();
        setFocusedIndex(count - 1);
        return focusableChildren[count - 1].focus();
      }

      case KEY_CODES.HOME: {
        e.preventDefault();
        setFocusedIndex(0);
        return focusableChildren[0].focus();
      }

      case KEY_CODES.ARROW_LEFT:
      case KEY_CODES.ARROW_UP: {
        e.preventDefault();
        setFocusedIndex(fIndex - 1 < 0 ? count - 1 : fIndex - 1);
        return focusableChildren[fIndex - 1 < 0 ? count - 1 : fIndex - 1].focus();
      }

      case KEY_CODES.ARROW_RIGHT:
      case KEY_CODES.ARROW_DOWN: {
        e.preventDefault();
        setFocusedIndex((fIndex + 1) % count);
        return focusableChildren[(fIndex + 1) % count].focus();
      }

      default:
        break;
    }

    if (typeof child.props.onKeyDown === 'function') {
      child.props.onKeyDown(e, k);
    }

    return setFocusedIndex(selectedTabIndex);
  };

  return (
    <ul
      className={classnames(`${CSS_PREFIX}-list`, {
        [`${CSS_PREFIX}--${orientation}`]: Boolean(orientation)
      }, className)}
      ref={ref}
      role={role}
      {...remainingProps}
    >
      {
        Children.map(children, (child, k) => (
          <TabItem role="none">
            {
              cloneElement(child, {
                key: k,
                onClick: e => handleClick(e, k, child),
                onKeyDown: e => handleKeyDown(e, k, child),
                selected: selectedTabIndex === k,
                tabIndex: Number.isInteger(focusedIndex)
                  ? focusedIndex === k ? 0 : -1
                  : k === 0 ? 0 : -1
              })
            }
          </TabItem>
        ))
      }
    </ul>
  );
});

TabList.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Orientation
   */
  orientation: oneOf(['vertical']),

  /**
   * @ignore
   */
  role: string,

  /**
   * Selected Tab Index
   */
  selectedTab: oneOfType([number, string])
};

TabList.defaultProps = {
  role: 'tablist'
};

TabList.displayName = 'TabList';

export default TabList;
