import React, { forwardRef, useEffect, useRef, useState } from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';

const CSS_PREFIX = 'vds-switch';
const CSS_STATE = 'vds-state';

const Switch = forwardRef(({
  className,
  checked,
  defaultChecked,
  defaultFocused,
  disabled,
  id = shortid.generate(),
  label,
  name,
  onBlur,
  onChange,
  onFocus,
  switchOffText,
  switchOnText,
  ...remainingProps
}, ref) => {
  const [isChecked, setChecked] = useState(checked || defaultChecked);
  const [isFocused, setFocused] = useState(defaultFocused);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  useEffect(() => {
    setChecked(checked);
  }, [checked]);

  const handleChange = event => {
    setChecked(event.target.checked);
    if (typeof onChange === 'function') {
      onChange(event);
    }
  };

  const handleFocus = event => {
    setFocused(true);
    if (typeof onFocus === 'function') {
      onFocus(event);
    }
  };

  const handleBlur = event => {
    setFocused(false);
    if (typeof onBlur === 'function') {
      onBlur(event);
    }
  };

  return (
    <div
      className={classnames(CSS_PREFIX, {
        [`${CSS_STATE}--active`]: isChecked,
        [`${CSS_STATE}--disabled`]: disabled,
        [`${CSS_STATE}--focus`]: isFocused
      }, className)}
      id={uid}
    >
      <div className={classnames(`${CSS_PREFIX}-value`)} aria-hidden="true">
        {isChecked ? switchOnText : switchOffText}
      </div>
      <label
        className={classnames(`${CSS_PREFIX}-label`)}
        htmlFor={`switch-control-${uid}`}
        id={`switch-control-label-${uid}`}
      >
        {label}
      </label>
      <input
        aria-checked={isChecked ? 'true' : 'false'}
        type="checkbox"
        id={`switch-control-${uid}`}
        className={classnames(`${CSS_PREFIX}-input`)}
        defaultChecked={isChecked}
        name={name}
        onBlur={handleBlur}
        onChange={handleChange}
        onFocus={handleFocus}
        ref={ref}
        role="switch"
        {...remainingProps}
      />
    </div>
  );
});

Switch.propTypes = {
  /**
   *   Checked
   */
  checked: bool,

  /**
   * @ignore
   */
  className: string,

  /**
   * Default checked
   */
  defaultChecked: bool,

  /**
   * Default focused
   */
  defaultFocused: bool,

  /**
  * Disabled
  */
  disabled: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
  * Label
  */
  label: node,

  /**
   * Name
   */
  name: string.isRequired,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * Callback fired when the value is changed.
   */
  onChange: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func,

  /*
   * Switch off text
   */
  switchOffText: node,

  /*
   * Switch on text
   */
  switchOnText: node
};

Switch.defaultProps = {
  switchOffText: 'Off',
  switchOnText: 'On'
};

Switch.displayName = 'Switch';

export default Switch;
