import React, { forwardRef } from 'react';
import { arrayOf, bool, node, oneOf, shape, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-data-table';
const CSS_STATE = 'vds-state';

const DataTable = forwardRef(({
  children,
  className,
  customizableColumns,
  dividerLines,
  keyValuePairs,
  reveal,
  revealFirstColumn,
  revealLastColumn,
  rowSelection,
  size,
  stickyHeader,
  ...remainingProps
}, ref) => (
  <table
    {...remainingProps}
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--customizable-columns`]: Boolean(customizableColumns),
      [`${CSS_STATE}--divider-lines`]: Boolean(dividerLines),
      [`${CSS_STATE}--key-value-pairs`]: Boolean(keyValuePairs),
      [`${CSS_STATE}--${size}`]: Boolean(size),
      [`${CSS_STATE}--reveal`]: Boolean(reveal),
      [`${CSS_STATE}--reveal-first-column`]: Boolean(revealFirstColumn),
      [`${CSS_STATE}--reveal-last-column`]: Boolean(revealLastColumn),
      [`${CSS_STATE}--${rowSelection}`]: Boolean(rowSelection),
      [`${CSS_STATE}--sticky-header`]: Boolean(stickyHeader)
    }, className)}
    ref={ref}
  >
    {children}
  </table>
));

DataTable.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Columns
   */
  columns: arrayOf(shape({})),

  /**
   * Customizable columns
   */
  customizableColumns: bool,

  /**
   * Data
   */
  data: arrayOf(shape({})),

  /**
   * Divider lines
   */
  dividerLines: bool,

  /**
   * Key value pairs
   */
  keyValuePairs: bool,

  /**
   * Reveal
   */
  reveal: bool,

  /**
   * Reveal first column
   */
  revealFirstColumn: bool,

  /**
   * Reveal last column
   */
  revealLastColumn: bool,

  /**
   * Row Selection
   */
  rowSelection: oneOf(['multi-select', 'single-select']),

  /**
   * Size
   */
  size: oneOf(['compact']),

  /**
   * Sticky Header
   */
  stickyHeader: bool
};

DataTable.displayName = 'DataTable';

export default DataTable;
