import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-th';

const Th = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <th
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </th>
));

Th.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

Th.displayName = 'Th';

export default Th;
